 .header{
     width: 100%;
     min-width: 100%;
     height: 90px;
     max-width: 1280px;
     margin: 0 auto;
     background-color: #ffffff;
     display: flex;

 }

.nav{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
}
.img{
    padding-left: 3%;
    align-self: center;
}
.img img{
    width: 100%;
}
.loginBlock{
    width: 100%;
}
.quote{
    position: absolute;
    background-image: url("../../img/цитата.png");
    width: 200px;
    height: 50px;
    border: 1px solid #ffffff;
    align-self: center;
    margin-right: 10px;
    display: none;
}
 @media screen and  (max-width : 520px) {
     .quote{
         display: block;
     }

 }
 @media screen and  (max-width : 420px) {
     .quote{
         
     }

 }