.block{
    width: 30%;
    height: 100%;
    display: flex;
    font-size: 13px;
}
.itemsSize{
    width: 33.33333333333333%;
    min-width: 100px;
    height: 100%;
    text-align: center;
    padding-top: 37px;
    box-sizing: border-box;

    font-weight: 700;
}
.link{
    text-decoration: none;
    color: #444444;
    text-transform: uppercase;
    padding: 8px 15px;
    border-radius: 3px;

}
.link:hover{
    background-color: #ffffff;
    color: #F4716A;
}
.item1{
    background-color:#FF8E88;
}
.item2{
    background-color:#FF7B74;
}
.item3{
    background-color:#F4716A;
}


@media screen and  (max-width : 960px) {
    .block{
        width: 100%;
    }
}
@media screen and  (max-width : 660px) {
    .block{
        font-size: 11px;
    }

}

@media screen and  (max-width : 520px) {
    .block{
        display: none;
    }

}