.flexBlock{
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
}
.itemImg {
    width: 30%;
    max-width: 300px;
    margin: 20px;
    text-align: center;
}
.itemImg img{
    width: 100%;
}
.itemInfo{
    width: 70%;
    max-width: 600px;
    margin: 20px;

}
.itemInfo p{
    font-size: 18px;
    font-weight: bold;
    text-transform: uppercase;
}
.spanItem{
    font-size: 14px;
    color: #000000;
}

.socialIcons b{
    font-size: 12px;
}
.socialIcons  img{
    width: 25px;
    vertical-align: middle;
}
.socialIcons {
    margin-top: 20px;
}


.input_btn{
    margin-top: 20px;
    background: #FF8E88 !important;
    color: #fff !important;
    font-size: 12px;
    text-align: center;
    font-weight: 600 !important;
    padding: 9px 14px;
    border-radius: 3px;
    text-transform: uppercase;
    cursor: pointer;
    border: none;
    outline: none;
}
.displayNone{
    display: none;
}
.span{
    display: block;
    padding: 10px;
    background-color: #ffffff;
}
.profileForm{
    margin: 20px 0;
    padding: 20px;
    width: 300px;
    max-width: 300px;
}
.spanForm{
    display: block;
    margin-bottom: 10px;
    font-weight: bolder;
}
.bForm{
    text-transform: none;
    font-size: 14px;
}
.profileDataBlock{
    width: 100%;
    display: grid;
    grid-template: repeat(2, auto)/repeat(3, auto);
    row-gap: 1em;
}
.profileDataBlock b{
    color: #444444;
}

.profileDataBlock b:nth-of-type(1){
    grid-column: 1/2;
    grid-row: 1;

}
.profileDataBlock b:nth-of-type(2){
    grid-column: 1/2;
    grid-row: 2;
}
.profileDataBlock b:nth-of-type(3){
    grid-column: 1/2;
    grid-row: 3;
}
.profileDataBlock span:nth-of-type(1){
    grid-column: 2/2;
    grid-row: 1;
}
.profileDataBlock span:nth-of-type(2){
    grid-column: 2/2;
    grid-row: 2;
}
.profileDataBlock span:nth-of-type(3){
    grid-column: 2/2;
    grid-row: 3;
}
@media screen and  (max-width : 600px) {
    .spanItem{
        font-size: 12px;
    }
    .profileDataBlock b{
        font-size: 12px;
    }
    .itemImg {
        margin: 10px;
    }
    .itemInfo{
        margin: 10px;
    }
}